.wrapper {
  max-width: 1280px;
  margin: 0 auto;
}

header {
  height: 100vh;
  font-family: lato;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: whitesmoke;
}

.headerContainer {
  display: flex;
  justify-content: space-between;
  
}

h1 {
  width: 50%;
}


.fancy {
  font-family: 'Cormorant Garamond';
  font-weight: 600;
  font-style: italic;
  font-size: 10vw;
}
.realEstate {
  text-transform: uppercase;
  font-size: 6vw;
}
.thin {
  font-weight: 300;
}


.scrollButton {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  height: 100%;
}

button {
  width: 50%;
  background-color: #0C0C0C; 
  border: 1px solid whitesmoke;
  color: whitesmoke;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

.buttonsContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.buttonsContainer button {
  margin: 10px;
}